.footer {
  background-color: var(--secondary-bg-color);
  &__main {
    padding-top: 56px;
    padding-bottom: 43px;
    @media screen and (min-width: 768px) {
      display: flex;
      justify-content: space-between;
      padding-top: 80px;
      padding-bottom: 90px;
    }
    @media screen and (min-width: 1280px) {
      padding-bottom: 95px;
    }
  }

  &__title {
    text-align: left;
    max-width: 260px;
    padding-left: 2px;
    padding-right: 46px;
    margin-bottom: 17px;

    @media screen and (min-width: 768px) {
      max-width: 350px;
    }
    @media screen and (min-width: 1280px) {
      min-width: 500px;
    }
  }
  &__pre-title {
    text-align: left;
    max-width: 260px;
    // margin-bottom: 43px;
    margin: 0 0 43px 0;

    @media screen and (min-width: 768px) {
      max-width: 340px;
      margin-bottom: 0px;
    }
    @media screen and (min-width: 1280px) {
      min-width: 500px;
    }
  }
  &__list {
    display: flex;
    justify-content: right;
    margin-bottom: 46px;
  }
  &__item:not(:last-child) {
    margin-right: 28px;
    @media screen and (min-width: 768px) {
      margin-right: 30px;
    }
    @media screen and (min-width: 1280px) {
      margin-right: 25px;
    }
  }
  &__link {
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    background-color: #fea5bb;
    width: 32px;
    height: 32px;
    transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1);
    &:hover,
    &:focus {
      background-color: var(--first-logo-color);
      animation: scaleSoc 1s infinite;
      animation-timing-function: linear;
    }
  }
  @keyframes scaleSoc {
    0% {
      transform: scale(1);
    }
    100% {
      transform: scale(1.3);
    }
  }

  &__social-icon {
    fill: var(--primary-bg-color);
  }
  &__tel {
    color: var(--first-logo-color);
    margin-left: 5px;
    &:hover,
    &:focus {
      animation: telRot 1.5s infinite;
      animation-timing-function: linear;
    }
  }
  @keyframes telRot {
    0% {
      color: #d41443;
      transform: scale(1);
    }
    100% {
      color: #ff0000;
      transform: scale(1.05);
    }
  }
  &__tel-txt {
    display: flex;
    justify-content: right;
    font-size: 12px;
    line-height: 1.86;

    @media screen and (min-width: 768px) {
      font-size: 14px;
    }
    @media screen and (min-width: 1280px) {
      font-size: 16px;
    }
  }
  &__reserved {
    line-height: 1.52;
    width: 145px;
    text-align: center;
    color: var(--third-text-color);
    margin-left: auto;
    margin-right: auto;
    @media screen and (min-width: 768px) {
      width: auto;
      line-height: 1.86;
    }
  }
  &__copyright {
    border-top: 1px solid #e1e1e1;
    padding: 25px 0;
    @media screen and (min-width: 768px) {
      padding-top: 30px;
      padding-bottom: 20px;
    }
    @media screen and (min-width: 1280px) {
      padding: 9px 0;
    }
  }
}
